<template>
  <div class="h-100 w-100 d-flex flex-column">
    <div class="row g-0 h-100 w-100">
      <div
        class="col-sm-6"
        v-for="(obj,key) in sensorDatas"
        :key="key"
        @click="obj.display=obj.display=='humi'? 'raw':'humi'"
      >
        <div class="device mx-2 my-2">
          <div class="d-flex flex-row justify-content-center">
            <h1 v-if="obj.display=='humi'">{{obj.data.humidity}}</h1>
            <h1 v-else>{{obj.data.raw}}</h1>
            <span v-if="obj.display=='humi'">%</span>
            <span v-else>analog</span>
          </div>
          <h4>{{obj.name}}</h4>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { GetMega2560RealTimeData, GetD1WIFIRealTimeData } from '@/apis/database';
export default {
  data() {
    return {

      fetchDataTimer: null,
      sensorDatas: {
        mega2650Data: { name: 'ROOM', display: 'humi', data: { humidity: -1 } },
        d1wifiData: { name: 'OutDoor', display: 'humi', data: { humidity: -1 } },
      }
    }
  },
  mounted() {
    this.fetchDataTimer = setInterval(async () => {
      this.sensorDatas.mega2650Data.data = await GetMega2560RealTimeData();
      this.sensorDatas.d1wifiData.data = await GetD1WIFIRealTimeData();
    }, 1000);
  },
  unmounted() {
    clearInterval(this.fetchDataTimer);
  }
}
</script>

<style>
.device {
  border: 1px solid whitesmoke;
  border-radius: 10px;
  box-shadow: 3px 3px 19px 2px black;
  padding: 40px;
  background-color: rgb(43, 43, 43);
}
.device h1 {
  font-size: 76px;
}
.device span {
  font-size: 30px;
  padding-top: 43px;
  padding-left: 13px;
}
.device h4 {
  font-weight: bold;
  text-decoration: underline;
}
</style>